<template>
  <div class="send-message-container">
    
    <van-field
      v-model="message"
      rows="12"
      clearable
      type="textarea"
      maxlength="1000"
      :placeholder="placeholder"
      show-word-limit
      :border="false"
    />

    <van-button
      @click="replyMessage"
      class="send-btn"
      color="linear-gradient(90deg,rgba(246,197,67,1),rgba(239,140,56,1))"      text="发送"
      round
    />
    
  </div>
</template>

<script>
import Vue from 'vue';
import { Field, Button, Toast } from 'vant';
import { apiSaveComment } from '@/service/api/comment';

Vue.use(Field).use(Button).use(Toast);

export default {
  data() {
    return {
      message: '',
      placeholder: '',
      userNick: null,
      msgId: null,
      msgUserId: null,
      srcCommentId: ''
    }
  },

  created() {
    document.title = '发表评论';
  },

  mounted() {
    this.receiveQuery();
  },

  methods: {
    // todo 保存 query 参数到组件中
    receiveQuery() {
      this.placeholder = '回复 ' + this.$route.query.userNick;
      this.userNick = this.$route.query.userNick;
      this.msgId = this.$route.query.msgId;
      this.msgUserId = this.$route.query.msgUserId;
      this.srcCommentId = this.$route.query.srcCommentId;
    },
    
    // todo 针对留言或者评论进行评论
    async replyMessage() {
      const saveCommentResponse = await apiSaveComment({
        msgId: this.msgId,
        msgUserId: this.msgUserId,
        content: this.message,
        srcCommentId: this.srcCommentId
      });
      if (!saveCommentResponse && saveCommentResponse.type === 'fail') return Toast('服务器错误，请联系客服');

      Toast( '发送成功' );
      this.message = '';
      this.$router.back();
    }
  }
}
</script>

<style lang="scss" scoped>
.send-message-container {
  width: 100%;
  height: 100%;
  background: rgba(246,246,246,1);

  .send-btn {
    width: calc(100% - 40px);
    margin: 20px 20px 0;
    height: 40px;
  }
}
</style>


